.CARD{
  width: 100%;
  display: flex;
  height: 200px;
  align-items: center;
  &-one{
    width: 150px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &Title{
      font-size: 16px;
      font-family: SourceHanSansSC-Bold, SourceHanSansSC;
      font-weight: bold;
      color: #333333;
      line-height: 24px;
      margin-top: 40px;
    }
    &Type{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 84px;
      height: 33px;
      background: #E3DAD0;
      border-radius: 4px;
      margin-top: 10px;
      font-size: 16px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: #342626;
      line-height: 24px;
      &.bgk{
        background: #EDF9E8;
        color: #4A8C1C;
      }
    }
    &Selected{
      font-size: 14px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: rgba(34, 34, 34, 0.4);
      line-height: 20px;
      margin-top: 33px;
    }
    &SelectedNum{
      font-size: 14px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: rgba(34, 34, 34, 1);
      line-height: 20px;
      margin-left: 10px;
    }
  }
  &-divider{
    width: 1px;
    height: 70%;
    color: #e0e0e0;
  }
  &-two{
    width: 450px;
    height: 100%;
    display: flex;
    align-items: center;
    &Content{
      width: 420px;
      height: 100%;
      display: flex;
      flex-direction: column;
      overflow: auto;
      &Item{
        display: flex;
        align-items: center;
        cursor: pointer;
        &Circle{
          width: 10px;
          height: 10px;
          border-radius: 5px;
          margin-top: 10px;
          &.showBgk{
            background: #096DD9;
          }
        }
        &Title{
          width: 360px;
          height: 42px;
          border-radius: 2px;
          line-height: 42px;
          margin-top: 10px;
          margin-left: 10px;
          position: relative;
          &.showBgk{
            background: #E0E7F5;
          }
          &Span{
            margin-left: 10px;
            font-size: 14px;
            color: #000;
            line-height: 20px;
            &.disabled{
              color: #9e9b9b;
            }
            &.selected{
              font-family: SourceHanSansSC-Bold, SourceHanSansSC;
              font-weight: bold;
              color: #096DD9;
            }
          }
        }
      }
    }
  }
  &-three{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    overflow: auto;
    &Content{
      max-width: 990px;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      position: relative;
      &Item{
        width: 320px;
        height: 42px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #f0f0f0;
        margin-top: 5px;
        margin-left: 10px;
      }
    }
  }
  &-empty{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &Font{
      font-size: 14px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: rgba(34, 34, 34, 0.7);
      line-height: 20px;
      margin-top: 5px;
    }
  }
  &-add{
    width: 21px;
    height: 21px;
    background: #096DD9;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.16);
    color: white;
    cursor: pointer;
    margin-left: 10px;
  }
  &-span{
    color: #000000;
    &.disabled{
      color: #9e9b9b;
    }
  }
  ::-webkit-scrollbar{
    width: 6px;
    height: 22px;
    background: white;
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb{
    width: 6px;
    height: 10px;
    background: #e0e0e0;
    border-radius: 4px;
  }
}

@primary-color: #1890ff;