.CheckContent{
  width: 100%;
  height: 100%;
  overflow: auto;
  &-header{
    display: flex;
    width: 100%;
    height: 80px;
    align-items: center;
  }
  &-card{
    width: 98%;
    height: 200px;
    border-radius: 5px;
    background: white;
    margin-left: 20px;
  }
  &-pop{
    width: 105px;
    height: 44px;
    background: #FFF2CD;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &-triangle{
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid #FFF2CD;
    position: absolute;
    left: 40px;
    bottom: -5px;
  }
}
@primary-color: #1890ff;